/* ==========================================================================
   LINKS / ELEMENTS
   Style de base de tous les liens et bouton du site
   ========================================================================== */

a {
    color: inherit;
    text-decoration: none;
    font-size: typo(18px);
    font-weight: 500;
    transition: all 0.3s ease-in;
}

.hover:hover {
    color: var(--color-mauve);
}